import React, { useState } from "react"
import AIBarGraph from "./ai/AIBarGraph"

function AIBarGraphs(props) {
  const [barGraphs] = useState([...props.graphs])

  return barGraphs.map((graph, index) => {
    return (
      <div
        className="metric grid grid-cols-9"
        key={graph.subTitle + "-" + index}
      >
        <div className="metric-header grid grid-flow-row auto-rows-min col-span-2">
          <h2 className="text-left font-semibold text-gray-900 dark:text-gray-200 text-xl subpixel-antialiased">
            {graph.subTitle}
          </h2>
        </div>
        <div className="col-span-7">
          <AIBarGraph {...graph} />
        </div>
      </div>
    )
  })
}

export default AIBarGraphs
